import React, { useState, useEffect } from 'react';
import debounce from 'lodash.debounce';
import Airtable from 'airtable';
import { Container, Form, Row, Col, Card, Badge, Button } from 'react-bootstrap';
import Spinner from 'react-bootstrap/Spinner';

const API_KEY = 'patFsxMUIOuOR6AD1.f5fdab387ba03d6ba92f7a3b893d09d38683a6ed428e84d333aedab1828e02d9';
const BASE_ID = 'appF9k97NW1HP35ae';
const TABLE_ID = 'tblvBBdfTUy2X1Lef';
const TAGS_FIELD_ID = 'fldQp01M2sk0vqpx7';
const TITLE_FIELD_ID = 'fld8bBjD73z1GugOx';
const base = new Airtable({apiKey: API_KEY}).base(BASE_ID);

const Search = () => {
  const [query, setQuery] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleSearch = debounce(async (term) => {
    setLoading(true);
    base(TABLE_ID).select({
      filterByFormula: `OR(SEARCH('${term}', ${TAGS_FIELD_ID}) > 0, SEARCH('${term}', ${TITLE_FIELD_ID}) > 0)`,
    }).eachPage(function page(records, fetchNextPage) {
      console.log(`got results for "${term}":`, records);
      setResults(records.map(record => {
        let thumbnailUrl = null;
        try {
          thumbnailUrl = record.fields['תצוגה מקדימה'][0].thumbnails.large.url;
          console.log('thumb:', thumbnailUrl);
        }
        catch {
          // pass
          console.log('no thumb for', record.id);
        }
        return {
          id: record.id,
          title: record.fields['כותרת'],
          thumbnailUrl: thumbnailUrl,
          tags: record.fields['תגיות'].split(', '),
          linkUrl: record.fields['קישורים'],
          rawData: record.fields
        }
      }));
      fetchNextPage();
    }, function done(err) {
      if (err) { console.error(err); }
      setLoading(false);
    });
  }, 300);

  const handleInputChange = (event) => {
    const value = event.target.value;
    setQuery(value);
    handleSearch(value);
  };

  useEffect(() => {
    handleSearch(query);
  }, []);

  return (
    <Container>
      <Form>
        <Form.Group as={Row}>
          <Col sm={8}>
            <Form.Control type="text" value={query} onChange={handleInputChange} placeholder="חפשו לפי תיאור או מילות מפתח" style={{width: '400px'}} class="form-control-lg mb-4" disabled={loading} />
          </Col>
        </Form.Group>
      </Form>
      <Row>
        {results.map((result) => (
          <Col key={result.id} sm={4}>
            <Card>
              <a href={result.linkUrl} class="d-block">
                <Card.Img variant="top" src={result.thumbnailUrl} class="CardImage" />
              </a>
              <Card.Body>
                <Card.Title><a href={result.linkUrl}>{result.title}</a></Card.Title>
                <Card.Text>{result.description}</Card.Text>
                <div>
                  {result.tags.map((tag) => (
                    <Badge key={tag}>{tag}</Badge>
                  ))}
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
        {!results.length && !loading && 'אין תוצאות'}
      </Row>
    </Container>
  );
};

export default Search;

